
import './App.css';
import Header from "./MyComponents/Header";
import {Footer} from "./MyComponents/Footer";
import {Todos} from "./MyComponents/Todos";
import {AddTodo} from "./MyComponents/AddTodo";
import {About} from "./MyComponents/About";

import React, { useState,useEffect } from 'react';

import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";


function App() {
    let initTodo;
    if (localStorage.getItem("todos")===null) {
        initTodo = [];        
    }
    else{
        initTodo = JSON.parse(localStorage.getItem("todos"));
    }
    let sln;
    const addTodo = (title,desc)=>{
        console.log("Adding the task...",title,"=>",desc)
        if (todoArray.length===0) {
            sln=0;
        }else{
            sln = todoArray[todoArray.length-1].sln + 1;
        }    
        const myTodo = {
            sln:sln,
            title:title,
            desc:desc
        }
        setTodoArray([...todoArray,myTodo]);
        console.log(myTodo);

    }

    const onDelete = (singleTodo)=>{
        setTodoArray(todoArray.filter((e)=>{
            return e!==singleTodo;
        }));
        localStorage.setItem("todos",JSON.stringify(todoArray));
    }
        
    const [todoArray, setTodoArray] = useState(initTodo);
    useEffect(() => {
        localStorage.setItem("todos",JSON.stringify(todoArray));

    }, [todoArray])


    return (
        <>
        <Router>
            <Header title="Mohit's TODO's List" searchBar={false} />
            <Switch>
            <Route exact path="/" render={()=>{
                return(
                    <>
                    <AddTodo addTodo={addTodo}/>
                    <Todos todoArray = {todoArray} onDelete = {onDelete} />
                    </>
                )
            }}>
          </Route>
          <Route exact path="/about">
            <About />
          </Route>
        </Switch>    
        <Footer/>
        </Router>    
        </>
        
    );
}

export default App;