import React from 'react'

export const TodoItem = ({singleTodo,onDelete}) => {
    return (
        <>
        <div>
            <h4>{singleTodo.title}</h4>
            <p>{singleTodo.desc}</p>
            <button className="btn btn-danger btn-sm" onClick= {()=>{onDelete(singleTodo)}}>Delete</button>
        </div>
        <hr/>
        </>
    )
}
