import React from 'react'
import {TodoItem} from "./TodoItem";


export const Todos = (props) => {
    const myStyle={
        minHeight:"37.5vh"
    }
    return (
        <div className="container" style={myStyle}>
            <h2 className="my-3">TODO's LIST</h2>
            {props.todoArray.length===0 ? "Do Something BRO!"://this is the main culprit
            props.todoArray.map((singleTodo)=>{
                return(
                <TodoItem singleTodo={singleTodo} key={singleTodo.sln} onDelete = {props.onDelete} /> 
            )})}    
            </div>
    )
}
